import ReviewTile from '../ReviewTile/ReviewTile';
import review from './ReviewList.module.css';
import {useState} from "react";

const ReviewList=()=>{
    const [showactive,setshowActive] = useState(false);
    const handleshow=()=>setshowActive(!showactive);
    const [show,setshow]=useState(false);
    const showButton=()=>{
        if(show===false)
        {setshow(true)
        document.getElementById(`${review.Wr}`).innerHTML='-';}
        else
        {setshow(false)
            document.getElementById(`${review.Wr}`).innerHTML='+';}
        
    }
    return(
        <div className={review.section}>
            <h1>Rating and Reviews</h1>
            <h2>Overall rating-</h2>
            <div className={review.write}>
                <label htmlFor="">Write Your Review <button id={review.Wr} onClick={()=>showButton()}> +</button></label>
                <div className={show ? review.writeIn : review.writeIn_hide}>
                    <textarea  name="comment"></textarea>
                    <input type='button' name='submit' value='submit'/>
                </div>
            </div>
            
            <div className={showactive?review.list:review.listactive} >
                <ReviewTile/>
                <ReviewTile/>
                <ReviewTile/>
                <ReviewTile/>
         </div>
         <lable className={review.show} onClick={()=>handleshow()}>Show more</lable>
         </div>
    );
        
}
export default ReviewList;