// import './Discription.css';
import { useState } from 'react';
import dis from './Discription.module.css';
const Discription=({onRouteChange})=>{
    const [shortDes,setshortDes]=useState(false);
    const showDes=()=>{
        if(shortDes===false)
        setshortDes(true)
        else
        setshortDes(false)
    }
    return(
        <div className={dis.about_pages}>
            <div className={dis.discription_bar} onClick={()=>showDes()}>Discription</div>
         <div className={shortDes?`${dis.dis}`:`${dis.dis} ${dis.hidedis}`}>
         <div className={dis.pdis}>Product Discription Or Details</div>
         <p>Lorem ipsum dolor sit amet consectetur, adipisicing elit. Illum reiciendis quaerat aliquid corrupti, iure fugiat totam, reprehenderit tenetur consectetur dicta ullam dignissimos voluptate assumenda. Aperiam porro repellat officiis culpa corrupti? Lorem ipsum dolor sit amet consectetur adipisicing elit. Dolor quaerat, amet asperiores aliquid doloribus ut tenetur nam aperiam minima quasi perspiciatis corrupti cupiditate possimus. Illo cumque quasi commodi maiores animi.
             Lorem ipsum dolor sit amet, consectetur adipisicing elit. Est, voluptate placeat cum, numquam fugiat distinctio nostrum veniam molestiae impedit maiores, ad officiis qui voluptatum necessitatibus eum nesciunt accusantium? Dolorum, voluptas?</p>
         
         <div className={dis.table}>
             <div className={dis.row}>
                 <div className={`${dis.col} ${dis.colhead}`} >Model</div>
                 <div className={`${dis.col} ${dis.colhead}`} >Id no.</div>
                 <div className={`${dis.col} ${dis.colhead}`} >Color</div>
                 <div className={`${dis.col} ${dis.colhead}`} >Dimensions</div>

             </div>
             <div className={dis.row}>
                 <div className={dis.col}>Black</div>
                 <div className={dis.col}>13B</div>
                 <div className={dis.col}>Classic</div>
                 <div className={dis.col}>20x3x20</div>
             </div>
         </div>
         <div className={dis.show}>show more</div>

         </div>
         
         
<div>
        </div>
    </div>
    );
}
export default Discription;