import { Link } from 'react-router-dom';
import { useState } from 'react';
import './About.css';
import star from '../../../ICONS/InProductPage/star.svg';
import cancel from '../../../ICONS/InProductPage/cancel.svg';

const AboutProduct=({onRouteChange,PD})=>{
    const [clear, setclear] = useState(false);
const handleFilter=(event)=>{
    if(event.target.value !== '')
    {setclear(true);}
    else if(event.target.value === '')
    setclear(false);
    else
    setclear(false);

}
    return(
    <div className='discription'>
   <div className='p1'> 
       <div className='pdetails'>
         <div className='pname'>{PD.name}</div>
         <div className='by'> By {PD.manufacturer}</div>
         <div className='RR'>&#9733;&#9733;&#9733;&#9734;&#9734; 256 Rating, 120 Reviews</div>
         <div className='price'>{PD.price}/-</div>
         <div className=''>
         <div className='offer'>Offers</div>
         <ul className='offer1'>
             <li>2% off on online payment.</li>
             <li>5% off on abc bank cards.</li>
             <li>free register subscrption</li>
             <li>Free Delevery on oorders above $50/-.</li>
             {/* <div className='num'>{`${PD.quantity}`}</div> */}

         </ul>
            <ul className='quantity'>
                 <li>
                     <input readOnly onClick={()=>{
                    if(document.getElementById('plus').value>0)
                    document.getElementById('plus').value--
                    PD.quantity=document.getElementById('plus').value;
                    }}  value='-' 
                    className='button'
                    />
                </li>
                <li>
                    <input id='plus' className='' type="number" readOnly value={`${PD.quantity}`}/>
                </li>
                <li>
                    <input readOnly onClick={()=>{document.getElementById('plus').value++
                        PD.quantity=document.getElementById('plus').value;}
                    }
                    className='button'value='+'/>
                </li>
            </ul>
         </div>
         </div>
         <div className='options'>
         <img src={star} className='fav'  name='favourite'  alt="star"/>
         <Link to={'cart'}><div onClick={()=>onRouteChange('cart')} className='btn-buy'>Add to Cart</div></Link>
         <Link to={'cart'}><div onClick={()=>onRouteChange('cart')} className='btn-buy'>Buy</div></Link>
         {/* <div className='btn-buy'>favourite</div> */}

         </div>

         </div>
         <div className='pavail'>
         <div className='avail'>Check Availability</div>
         <div className='pinput'>
           <input onChange={handleFilter}
            id='areacode' className='pin' placeholder='enter area pincode' type='text' size='20px'/>
           <img src={cancel} alt="" className={clear?'active':'unactive'}  style={{
            height: '1em',
            transform:' translate(-30px, 1px)',
            width: '1em'}}
            onClick={()=>{
                    document.getElementById('areacode').value=''
                    document.getElementById('av').innerHTML=' '
            }}  size='10px' /> 
            <input
            onClick={()=>{
                if(document.getElementById('areacode').value==='2020'){
                document.getElementById('av').innerHTML='available'}
                else if(document.getElementById('areacode').value===''){
                    document.getElementById('av').innerHTML=' ' 
                }
                else {
                    document.getElementById('av').innerHTML=' NOT available' 
                }
            }}className='psub' type='submit' size='20px' value='check'/> 
            <span id='av'></span> 
         </div>
         
         </div>
         
         <div className='Varientpcolor'>
             <div className='pvarient'>Varients</div>
             <div className='varients'>
            <div className='vname'>
             <img className='preimg' alt='product' src='https://www.montblanc.com/variants/images/34480784411791124/A/w570.jpg'/>
             <div className='vname'>Blue</div>
             </div>
             <div className='vname'>
             <img className='preimg' alt='product' src='https://www.montblanc.com/variants/images/34480784411791124/A/w570.jpg'/>
             <div className='vname'>Black</div>
             </div>
             <div className='vname'>
             <img className='preimg' alt='product' src='https://www.montblanc.com/variants/images/34480784411791124/A/w570.jpg'/>
             <div className='vname'>red</div>
             </div>           
             </div>
         
         </div>
    </div>
    );
}
export default AboutProduct;