import React from "react";
import './SideNav.css';
import { CatagoryItems,SideMenu2 } from "./SideNavData";
import  {Link}  from "react-router-dom";
const SideNav=({onRouteChange})=>{
    const mobileMenu=()=> {
        document.querySelector(".hamburger").classList.toggle("active");
        document.querySelector(".nav-menu").classList.toggle("active");
    }
    const closeMenu=()=>{
        document.querySelector(".hamburger").classList.remove("active");
        document.querySelector(".nav-menu").classList.remove("active");
        document.getElementById("products").checked = false;
    }
    document.querySelectorAll(".navi-link").forEach(n => n.addEventListener("click", closeMenu));
    return(
        <nav className="navbar">
            <ul className="nav-menu">
            <div className="rrr">

                <li className="g">
                    <img className='profile-image' src='data:image/jpeg;base64,' alt="profile" />
                    <p className="nav-link nav-logo profile">My Profile</p>
                </li>
                <li className="nav-item" >
                    <nav className="navigation">
                    <ul className="mainmenu  nav-item">
                     <li className="navi-link" ><Link to={'/E-shop/'}><p>Home</p></Link></li>
                        <li className="navi-link"><p>Offers Zone</p></li>
                        <li className="navi-link"><p>My Orders</p></li>
                        <li ><label id='label' htmlFor="products">Category</label><input type="checkbox" id="products"/>
                        <ul className="submenu nav-links">
                            {
                            CatagoryItems.map((items,index)=>{
                            return(
                            <li  key={index} className="navi-link">
                                <Link  to={`/E-shop/${items.route}`}>
                                    <p>{items.title}</p>
                            </Link>
                            </li>
                            )
                        })
                    }
            </ul>
            </li>
            {
                SideMenu2.map((items,index)=>{
                    return(
                        <li  key={index} className="navi-link">
                            <Link  to={`/E-shop/${items.route}`}>
                                <p>{items.title}</p>
                            </Link>
                        </li>
                            );
                        })
            }
            </ul>
             </nav>
                </li>
</div>

            </ul>    
            {/* hamburger menu icon */}      
            <div className="hamburger" onClick={()=>mobileMenu()}>
                <span className="bar"></span>
                <span className="bar"></span>
                <span className="bar"></span>
            </div>

        </nav>
    );
}
export default SideNav;